@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap);
@import url(https://unpkg.com/boxicons@2.0.9/css/boxicons.min.css);
li{
  position: relative;
  margin: 8px 0;
  list-style: none;
}

li .tooltip{
  position: absolute;
  top: -20px;
  left: calc(100% + 15px);
  z-index: 3;
  background: #fff;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3);
  padding: 6px 12px;
  border-radius: 4px;
  font-size: 15px;
  font-weight: 400;
  opacity: 0;
  white-space: nowrap;
  pointer-events: none;
  transition: 0s;
}

li:hover .tooltip{
  opacity: 1;
  pointer-events: auto;
  transition: all 0.4s ease;
  top: 50%;
  transform: translateY(-50%);
}

.open li .tooltip{
  display: none;
}

li a{
  display: flex;
  height: 100%;
  width: 100%;
  border-radius: 12px;
  align-items: center;
  text-decoration: none;
  transition: all 0.4s ease;
  background: #11101D;
}

li a:hover{
  background: #FFF;
}

li a .text{
  color: #fff;
  font-size: 15px;
  font-weight: 400;
  white-space: nowrap;
  opacity: 0;
  pointer-events: none;
  transition: 0.4s;
}

.open li a .text{
  opacity: 1;
  pointer-events: auto;
}

li a:hover .text,
li a:hover i{
  transition: all 0.5s ease;
  color: #11101D;
}

li i{
  height: 50px !important;
  line-height: 50px !important;
  font-size: 18px !important;
  border-radius: 12px !important;
}

li.user{
  position: fixed;
  height: 60px;
  width: 78px;
  left: 0;
  bottom: -8px;
  padding: 10px 14px;
  background: #1d1b31;
  transition: all 0.5s ease;
  overflow: hidden;
}

.open li.user{
  width: 250px;
}

li .user-details{
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
}

li img{
  height: 45px;
  width: 45px;
  object-fit: cover;
  border-radius: 6px;
  margin-right: 10px;
}

li.user .name,
li.user .status{
  font-size: 15px;
  font-weight: 400;
  color: #fff;
  white-space: nowrap;
}

li.user .status{
  font-size: 12px;
}

.user #log_out{
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  background: #1d1b31;
  width: 100%;
  height: 60px;
  line-height: 60px;
  border-radius: 0px;
  transition: all 0.5s ease;
  cursor: pointer;
}

.open .user #log_out{
  width: 50px;
  background: none;
}
/* Google Font Link */
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins" , sans-serif;
}

.sidebar{
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 78px;
  background: #11101D;
  padding: 6px 14px;
  z-index: 99;
  transition: all 0.5s ease;
}

.sidebar.open{
  width: 250px;
}

.sidebar .header{
  height: 60px;
  display: flex;
  align-items: center;
  position: relative;
}

.sidebar .header img {
  height: 45px;
  width: 45px;
  object-fit: cover;
  border-radius: 6px;
  margin-right: 10px;
}

.sidebar .header img,
.sidebar .header .icon{
  opacity: 0;
  transition: all 0.5s ease;
}

.sidebar .header .title{
  color: #fff;
  font-size: 20px;
  font-weight: 600;
  opacity: 0;
  transition: all 0.5s ease;
}

.sidebar.open .header .icon,
.sidebar.open .header img,
.sidebar.open .header .title{
  opacity: 1;
}

.sidebar .header #btn{
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  font-size: 22px;
  transition: all 0.4s ease;
  font-size: 23px;
  text-align: center;
  cursor: pointer;
  transition: all 0.5s ease;
}

.sidebar.open .header #btn{
  text-align: right;
}

.sidebar i{
  color: #fff;
  height: 60px;
  min-width: 50px;
  font-size: 28px;
  text-align: center;
  line-height: 60px;
}

.sidebar .nav-list{
  margin-top: 20px;
  height: 100%;
}

@media (max-width: 420px) {
  .sidebar li .tooltip{
    display: none;
  }
}


section{
  position: relative;
  background: #E4E9F7;
  min-height: 100vh;
  top: 0;
  left: 78px;
  width: calc(100% - 78px);
  transition: all 0.5s ease;
  z-index: 2;
}
.open ~ section{
  left: 250px;
  width: calc(100% - 250px);
}
section h1{
  display: inline-block;
  color: #11101d;
  font-size: 25px;
  font-weight: 500;
  margin: 18px
}

